@use "../abstracts/" as *;
.orderModal {
  position: fixed;
  display: table;
  top: 50%;
  left: 50%;
  padding: rem(48);
  border-radius: rem(8);
  transform: translate(-50%, -50%);
  background-color: var(--clr-light-1);

  &__tickIcon {
    width: rem(64);
    height: rem(64);
    margin-bottom: rem(33);
    color: var(--clr-primary);
  }
  .heading--3 {
    margin-bottom: rem(24);
    max-width: rem(284);
  }
  & > .body {
    margin-bottom: rem(33);
    opacity: 0.5;
  }
  &__items {
    margin-bottom: rem(46);
    display: flex;
    justify-content: space-between;
    border-radius: rem(8);
    overflow: hidden;
    width: 100%;
  }
  .btn--primary {
    width: 100%;
  }
  &__details {
    background-color: var(--clr-light-3);
    padding: rem(24);
    flex: 1;
  }
  &__image {
    width: rem(50);
    height: rem(50);
    margin-right: rem(16);
  }
  &__firstProduct {
    display: flex;
    padding-bottom: rem(12);
  }
  &__moreItems {
    text-align: center;
    font-size: rem(12);
    line-height: rem(16);
    letter-spacing: rem(-0.214286);
    opacity: 0.5;
    padding-top: rem(12);
    border-top: 1px solid rgba(#000000, 0.08);
  }
  &__total {
    background-color: #000000;
    width: 40%;
    color: white;
    text-transform: uppercase;
    padding: rem(41) rem(32);
    :first-child {
      opacity: 0.5;
      margin-bottom: rem(8);
    }
  }
  @include respond(tab-port) {
    &__total {
      padding: rem(41, $fs-s) rem(24, $fs-s);
    }
    min-width: 70%;
  }
  @include respond(phone) {
    padding: rem(24, $fs-s);
    min-width: calc(100% - 24px);
    &__items {
      flex-direction: column;
      margin-bottom: rem(24, $fs-s);
    }
    &__total {
      width: 100%;
      padding: rem(16, $fs-s) rem(24, $fs-s);
    }
  }
}
